<style lang="postcss" scoped>
.home-container{

}
</style>
<template>
<section class="home-container">
    <MasterPage title="所有房源">
        <div slot="title-icon">
            <Icon type="md-home" />
        </div>
        <div slot="searchContent" class="search-content-slot">
            <Form :model="formItem" :label-width="80">
              <Row>
                <Col span="12">
                  <FormItem label="筛选：">
                    <Select v-model="searchKey">
                        <Option :value='item.key' :key="item.index" v-for="item in columns1">{{item.title}}</Option>
                    </Select>
                </FormItem>
                </Col>
                <Col span="12">
                  <FormItem label="定向搜索：">
                    <Input @on-change="handleClear" v-model="searchValue" placeholder="输入..."/>
                </FormItem>
                </Col>
              </Row>
          </Form>
        </div>
        <div slot="btns">
          <Button type="primary" icon="ios-search" @click="handleSearch">查询</Button>
        </div>
        <div slot="paddingContent">
          <Table :columns="columns1" :data="data1"></Table>
        </div>
        <div slot="pager">
            <Page :total="100" />
        </div>
    </MasterPage>
</section>
</template>
<script>
import MasterPage from '@/components/Master'

export default {
    components:{
        MasterPage
    },
    data(){
      return{
        formItem: {
            input: '',
            select: '',
            radio: 'male',
            checkbox: [],
            switch: true,
            date: '',
            time: '',
            slider: [20, 50],
            textarea: ''
        },
        searchKey: 'name',
        searchValue: '',
        data : [],
        columns1: [
            {
                title: '编号',
                key: 'name'
            },
            {
                title: '楼盘名',
                key: 'age'
            },
            {
                title: '单元',
                key: 'address'
            },
            {
                title: '房号',
                key: 'num'
            },
            {
                title: '状态',
                key: 'state'
            }
        ],
        data1: [
            {
                name: 'BSR33010',
                age: "明洲北苑",
                address: '26栋1单元',
                num: '1001',
                state: '售出'
            },
            {
                name: 'BSR33031',
                age: "明洲北苑",
                address: '5栋2单元',
                num: '1102',
                state: '售出'
            },
            {
                name: 'BSR34513',
                age: "明洲南苑",
                address: '14栋1单元',
                num: '5005',
                state: '待售'
            },
            {
                name: 'BSR33034',
                age: "明洲西苑",
                address: '21栋1单元',
                num: '6001',
                state: '售出'
            },
             {
                name: 'BSR33062',
                age: "明洲北苑",
                address: '1栋2单元',
                num: '1201',
                state: '待签约'
            },
             {
                name: 'BST35026',
                age: "明洲北苑",
                address: '12栋2单元',
                num: '2302',
                state: '待售'
            },
            {
                name: 'BSR33036',
